.rating {
	padding-bottom: 20px;
	@media (max-width: 900px) {
		width: 100vw;
	}
}

.rating ul {
	margin-bottom: 0px;
}

.rating__list {
	@media (max-width: 450px) {
		margin-top: 85px;
	}
}

.rating__title {
	background-color: #060c14;
	display: grid;
	grid-template-columns: 1.2fr 2fr 2fr 2fr 2fr 2fr;
	font-size: 15px;
	list-style: none;

	text-align: center;
	z-index: 3;
	border-bottom: 2px solid rgba(255, 255, 255, 0.459);
	@media (max-width: 600px) {
		font-size: 12px;
	}
	@media (max-width: 450px) {
		position: fixed;

		top: 0vh;
		margin: 0px;
		padding-top: 50px;
		font-size: 10px;
		padding-inline: 3px;
		box-sizing: border-box;
	}
}

.rating__title li h3 {
	margin: 0px 0px 8px 0px;
	@media (max-width: 450px) {
	}
}

.rating__title li {
	padding-inline: 3px;
	box-sizing: border-box;
	align-self: end;
}

.rating__item {
	display: grid;

	grid-template-columns: 1.2fr 2fr 2fr 2fr 2fr 2fr;
	height: 120px;
	font-size: 15px;
	border-bottom: 2px solid rgba(255, 255, 255, 0.459);
	align-items: center;
	justify-items: center;
	text-align: center;
	list-style: none;
	@media (max-width: 600px) {
		font-size: 12px;
	}
	@media (max-width: 450px) {
		font-size: 10px;
		height: 90px;
	}
}

.rating__image {
	width: 100px;
	aspect-ratio: 1/1;
	border-radius: 10%;
	@media (max-width: 450px) {
		width: 70px;
	}
}

.rating__achievements {
	display: inline-block;
	display: grid;

	grid-template-columns: repeat(4, auto);
	align-items: end;
	@media (max-width: 450px) {
		grid-template-columns: repeat(3, min-content);
		max-width: 81px;
		height: 68px;

		overflow-x: auto;
	}
}

.rating__achievement {
	align-self: center;
}

.rating__achievements_title {
	display: none;
	position: absolute;
	width: 110px;
	background-color: rgba(0, 0, 0, 0.7);
	padding: 5px;
	border-radius: 3px;
	font-size: 15px;
	z-index: 2;
	@media (max-width: 450px) {
		font-size: 10px;
		width: 60px;
	}
}

.rating__achievements_image:hover + .rating__achievements_title {
	display: block;
}

.rating__achievements_image {
	position: relative;
	max-width: 40px;
	padding: 1px;
	@media (max-width: 551px) {
		max-width: 30px;
	}
	@media (max-width: 450px) {
		max-width: 25px;
	}
}

.rating__memo_conteiner {
	margin-top: 20px;
	padding-inline: 10px;
	text-align: left;

	border-radius: 1rem;
}

.rating__name {
	padding-inline: 3px;
}

.rating__memo {
	font-size: 15px;

	margin: 0;
	padding: 5px;
}

.rating__link {
	text-decoration: none;
	color: rgb(44, 149, 235);
}
.rating__link:hover {
	text-decoration: underline;
}
